.App {
  text-align: center;
}
.quadContainer {
  padding:0;
  margin:0;
  height:100%;
  min-height:100%;
 }
 .quarter{
  width:50%;
  height:100%;
  max-height: 600 px;
  float:left;
}
.contents{
  height:50%;
  width:100%;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
